.admin {
  &__title {
    display: flex;
    flex-direction: column;
  }
  &__subtitle {
    display: block;
    font-size: 22px;
    color: #FF4D1C;
    margin-top: 5px;
  }
  &__table {
    & .dataTables_wrapper .dataTables_paginate .paginate_button {
      background: none;
      border-radius: 5px;
      transition: all 0.4s;
      &:hover {
        background: #020202;
        color: #ffffff !important;
      }
    }
    & button.dt-button, div.dt-button, a.dt-button, input.dt-button {
      background: none;
      border-radius: 5px;
      transition: all 0.4s;
      &:hover {
        background: #22a019;
        color: #ffffff !important;
        border: 1px solid #22a019;
      }
    }
    & a {
      color: #FF4D1C;
      text-decoration: underline;
      &:hover {
        color: #020202;
      }
    }
    & .dataTables_info {
      display: none;
    }
    & td {
      border-bottom: 1px solid #c4c4c4 !important;
      padding-top: 15px !important;
      padding-bottom: 15px !important;
      color: #333333;
    }
    & .dataTables_wrapper .dataTables_paginate {
      margin-top: 15px;
      float: none;
      text-align: center;
    }
    & thead {
      & td {
        font-weight: 500;
        font-size: 18px;
      }
    }
  }
}
